.new-expense {
    background-color: lightblue;
    padding: 1rem;
    margin: 2rem auto;
    width: 50rem;
    max-width: 95%;
    border-radius: 12px;
    text-align: center;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
}

.new-expense button {
    font: inherit;
    cursor: pointer;
    padding: 1rem 2rem;
    border: 1px solid black;
    border-radius: 12px;
    margin-right: 1rem;
    font-weight: bold;
}

.new-expense button:hover,
.new-expense button:active {
    background-color: orange;
}

